import PropTypes from "prop-types";
import React from "react";
import { connect } from "react-redux";
import MovieImageGallery from "../../components/product/MovieImageGallery";
import MovieDescriptionInfo from "../../components/product/MovieDescriptionInfo";

const MovieImageDescription = ({
  spaceTopClass,
  spaceBottomClass,
  product,
  wishlistItems,
}) => {
  const wishlistItem = wishlistItems.filter(
    wishlistItem => wishlistItem.id === product.id
  )[0];
  return (
    <div
      className={`shop-area ${spaceTopClass ? spaceTopClass : ""} ${
        spaceBottomClass ? spaceBottomClass : ""
      }`}
    >
      <div className="container">
        <div className="row">
          <div className="col-lg-6 col-md-6">
            <MovieImageGallery product={product} />
          </div>
          <div className="col-lg-6 col-md-6">
            {/* product description info */}
            <MovieDescriptionInfo
              product={product}
              wishlistItem={wishlistItem}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

MovieImageDescription.propTypes = {
  product: PropTypes.object,
  spaceBottomClass: PropTypes.string,
  spaceTopClass: PropTypes.string,
  wishlistItems: PropTypes.array
};

const mapStateToProps = state => {
  return {
    wishlistItems: state.wishlistData,
  };
};

export default connect(mapStateToProps)(MovieImageDescription);
