import PropTypes from "prop-types";
import React from "react";
import { connect } from "react-redux";
import { addToWishlist } from "../../redux/actions/wishlistActions";
import Rating from "./sub-components/ProductRating";

const MovieDescriptionInfo = ({
  product,
}) => {

  return (
    <div className="product-details-content ml-70">
      <h2>{product.Title}</h2>
      <div className="product-details-price">
        {product.Year}
      </div>
      <div className="pro-details-rating-wrap">
        <div className="pro-details-rating">
          <Rating ratingValue={parseInt(product.imdbRating) / 2} />
        </div>
      </div>
      <div className="pro-details-list">
        <p>{product.Plot}</p>
        <hr />
        <p>Released Time: {product.Released}</p>
        <p>Run Time: {product.Runtime}</p>
        <p>Genre: {product.Genre}</p>
        <p>Director: {product.Director}</p>
        <p>Writer: {product.Writer}</p>
        <p>Actors: {product.Actors}</p>
        <p>Language: {product.Language}</p>
        <p>Country: {product.Country}</p>
        <p>Awards: {product.Awards}</p>
        <p>Votes: {product.imdbVotes}</p>
        <p>Metascore: {product.Metascore}</p>
        <p>Production: {product.Production}</p>
        <p>Website: {product.Website}</p>
      </div>
      <div className="pro-details-social">
        <ul>
          <li>
            <a href="//facebook.com">
              <i className="fa fa-facebook" />
            </a>
          </li>
          <li>
            <a href="//dribbble.com">
              <i className="fa fa-dribbble" />
            </a>
          </li>
          <li>
            <a href="//pinterest.com">
              <i className="fa fa-pinterest-p" />
            </a>
          </li>
          <li>
            <a href="//twitter.com">
              <i className="fa fa-twitter" />
            </a>
          </li>
          <li>
            <a href="//linkedin.com">
              <i className="fa fa-linkedin" />
            </a>
          </li>
        </ul>
      </div>
    </div>
  );
};

MovieDescriptionInfo.propTypes = {
  product: PropTypes.object,
  wishlistItem: PropTypes.object
};

const mapDispatchToProps = dispatch => {
  return {
    addToWishlist: (item, addToast) => {
      dispatch(addToWishlist(item, addToast));
    }
  };
};

export default connect(null, mapDispatchToProps)(MovieDescriptionInfo);
