import PropTypes from "prop-types";
import React, { Fragment } from "react";
import { LightgalleryProvider} from "react-lightgallery";
import config from "../../config";

const MovieImageGallery = ({ product }) => {

  return (
    <Fragment>
      <div className="product-large-image-wrapper">
        <LightgalleryProvider>
          <div className="single-image" style = {{ boxShadow: "0 4px 8px 0 rgba(0,0,0,0.2)"}} >
            <img
              src={process.env.PUBLIC_URL + product.Poster === "N/A" ? `http://${config.SEVER_ADDRESS}:${config.PORT}/images/noposter.jpg` : process.env.PUBLIC_URL + product.Poster}
              className="img-fluid border p-2"
              alt=""
            />
          </div>
        </LightgalleryProvider>
      </div>
    </Fragment>
  );
};

MovieImageGallery.propTypes = {
  product: PropTypes.object
};

export default MovieImageGallery;
