import Axios from "axios";
import PropTypes from "prop-types";
import React, { Fragment, useEffect, useState } from "react";
import MetaTags from "react-meta-tags";
import { connect } from "react-redux";
import LayoutOne from "../layouts/LayoutOne";
import MovieImageDescription from "../wrappers/product/MovieImageDescription";
import { fetchPendFlag } from "../redux/actions/pendActions";
import config from '../../src/config';
const MovieDetail = ({setPendFlag}) => {
  const [product, setProduct] = useState({});
  useEffect(() => {
    var arr = window.location.href.split('/');
    var imdbID = arr[arr.length - 1];
    const URL = `http://${config.SEVER_ADDRESS}:${config.PORT}/${imdbID}`;
    
    Axios.post(URL).then((res) => {
        if(res.data.Response === "True") {
          setProduct(res.data);
        } else {
          alert(res.data.Error);
        }
    }).catch(error => {
        console.log(error);
    });
  }, []);
  return (
    <Fragment>
      <MetaTags>
        <title>MDB-API | DETAIL PAGE</title>
        <meta
          name="description"
          content="Product page"
        />
      </MetaTags>

      <LayoutOne headerTop="visible">
        <MovieImageDescription
          spaceTopClass="pt-100"
          spaceBottomClass="pb-100"
          product={product}
        />
      </LayoutOne>
    </Fragment>
  );
};

MovieDetail.propTypes = {
  location: PropTypes.object,
  setPendFlag: PropTypes.func
};

const mapStateToProps = (state, ownProps) => {
  const itemId = parseInt(ownProps.match.params.id);
  return {
    product: state.productData.products.filter(
      single => single.id === itemId
    )[0]
  };
};
const mapDispatchToProps = dispatch => {
  return {
      setPendFlag: (flag) => {
          dispatch(fetchPendFlag(flag));
      }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(MovieDetail);
